/* App.css */
.App {
  text-align: center;
}

.container-fluid {
  /* Remove background color */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* Remove background color */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dashboard-container {

  padding: 10px;
}

/* Navbar styles */
.navbar-nav {
  display: flex;
  background-color: #28a745;
  padding: 20px;
}

.navbar-nav .nav-link {
  padding: 10px 20px;
  margin: 0 10px;
  text-decoration: none;
  background-color: #007bff; /* Add background color to nav links */
}

.navbar-nav .nav-link:hover {
  background-color: #0056b3; /* Adjust hover background color */
}

.btn-3d {
  color: #fff;
}

.btn-blue {
  background-color: #007bff;
}

.btn-green {
  background-color: #28a745;
}

.btn-orange {
  background-color: #fd7e14;
}

.btn-purple {
  background-color: #6f42c1;
}

.btn-3d:hover {
  transform: translateY(-2px);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

/* Remove dots associated with list items */
.navbar-nav .nav-item {
  list-style-type: none;
}
.ml-2{
  display: flex;
  margin-bottom: 20px;
}